import React from "react";
import { About } from "./features/profile/About";
import { BrowserRouter, Switch, Route } from "react-router-dom";
import { Resume } from "./features/profile/Resume";
import { Portfolio } from "./features/profile/Portfolio";
import { Services } from "./features/profile/Services";
import { BlogHome } from "./features/blog/BlogHome";
import { Contact } from "./features/profile/Contact";
import { Project } from "./features/profile/Project";
import { BlogHomeAlt } from "./features/blog/BlogHomeAlt";
import { BlogPost } from "./features/blog/BlogPost";
import { BlogList } from "./features/blog/BlogList";
import { ArticlesHome } from "./features/articles/ArticlesHome";
import { ArticlePost } from "./features/articles/ArticlePost";
import { SideBar } from "./features/nav/SideBar";
import { ConfigPanel } from "./common/layout/ConfigPanel";
import { ArticlesNavigation } from "./features/articles/navigation/ArticlesNavigation";

function App() {
  return (
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      <SideBar />
      <ArticlesNavigation />
      <Route exact path="/" component={ArticlesHome} />
      <Switch>
        <Route exact path="/about/" component={About} />
        <Route exact path="/portfolio/" component={Portfolio} />
        <Route exact path="/services/" component={Services} />
        <Route exact path="/resume/" component={Resume} />
        <Route exact path="/contact/" component={Contact} />
      </Switch>
        <Switch>
        <Route exact path="/blog-home/" component={BlogHome} />
        <Route exact path="/blog-alt-home/" component={BlogHomeAlt} />
        <Route exact path="/blog-post/" component={BlogPost} />
        <Route exact path="/blog-list/" component={BlogList} />
        <Route exact path="/project/" component={Project} />
        <Route exact path="/articles/:type/" component={ArticlesHome} />
        <Route exact path="/post/:type/:name/" component={ArticlePost} />
      </Switch>

      <ConfigPanel />
    </BrowserRouter>
  );
}

export default App;
