import React from "react";
import { Helmet } from "react-helmet";

interface IProps {
  title: string;
  description: string;
  type?: string;
}
export const HtmlTags: React.FC<IProps> = ({ title, description, type }) => {
  return (
    <Helmet>
      <meta name="description" content={description} />
      <meta name="title" content={title} />
      <meta name="twitter:card" content="summary_large_image" />
      <meta name="medium" content="mult" />
      <meta
        name="viewport"
        content="width=device-width, initial-scale=1.0, minimum-scale=1.0"
      />
      <meta property="fb:app_id" content="525384784808516" />
      <meta http-equiv="X-UA-Compatible" content="IE=edge" />
      <meta name="viewport" content="width=device-width, initial-scale=1.0" />

      <meta property="og:title" content={title} />
      <meta property="og:url" content={window.location.href} />
      <meta property="og:description" content={description} />
      <meta
        property="og:image"
        content="http://manish.guru/assets/images/profile.jpg"
      />
      <meta property="og:type" content={type ? type : "article"} />
      <meta property="og:site_name" content="Manish" />
      <meta property="og:locale" content="en_US" />

      <meta name="twitter:domain" content="www.manish.guru" />
      <meta name="twitter:title" content={title} />
      <meta name="twitter:url" content={window.location.href} />
      <meta name="twitter:description" content={description} />
      <meta
        name="twitter:image"
        content="http://manish.guru/assets/images/profile.jpg"
      />
      <meta name="twitter:site" content="@manishkrch" />

      <meta name="author" content="Manish Kumar Choudhary" />
      {/* <meta itemprop="name" content="Code with Manish" />
      <meta itemprop="url" content="http://manish.guru/" />
      <meta
        itemprop="description"
        content="Hi, my name is Manish Kumar Choudhary and I'm a senior system engineer. Welcome to my personal website!"
      />
      <meta
        itemprop="image"
        content="http://manish.guru/assets/images/profile.jpg"
      /> */}
      <meta name="application-name" content="Manish" />

      <meta name="msapplication-TileColor" content="#EDECE6" />
      <meta
        name="msapplication-TileImage"
        content="http://manish.guru/assets/images/manish_144.png"
      />
      <meta
        name="msapplication-square70x70logo"
        content="http://manish.guru/assets/manish_70.png"
      />
      <meta
        name="msapplication-square150x150logo"
        content="http://manish.guru/assets/images/manish_150.png"
      />
      <meta
        name="msapplication-wide310x150logo"
        content="http://manish.guru/assets/images/manish_310_150.png"
      />
      <meta
        name="msapplication-square310x310logo"
        content="http://manish.guru/assets/images/manish_310.png"
      />
    </Helmet>
  );
};
